// src/features/shopping-cart/components/AddToCartButton.tsx
import React from 'react'
import styles from '../../product-catalog/components/ProductCard.module.css'
import { Product } from '../../../types/product'

interface AddToCartButtonProps {
  product: Product
  onAddToCart: (product: Product) => void
}

const AddToCartButton: React.FC<AddToCartButtonProps> = ({ product, onAddToCart }) => {
  const handleClick = (event: React.MouseEvent) => {
    event.stopPropagation() // Impede a propaga√ß√£o do evento para o Link pai
    onAddToCart(product)
  }

  return (
    <button className={styles.addToCartButton} onClick={handleClick}>
      Adicionar ao Carrinho
    </button>
  )
}

export default AddToCartButton
